import React from "react";
import OnlineUtils from "eshop/utils/OnlineUtils";
import TooltipFromHtml from "eshop/modules/core/components/ui/TooltipFromHtml";
import Price from "eshop/modules/core/components/ui/Price";
import {StylesUtils} from "eshop/modules/core/utils/StylesUtils";
import OfferType from "../../constants/OfferTypeEnum";

export class Details extends React.Component {
    constructor(props) {
        super(props);
        this.details = null;
        this.preprocess();
    }

    makeReactElement(html) {
        return TooltipFromHtml.conditionalRender(html);
    }

    componentDidUpdate() {
        this.preprocess();
    }

    preprocess() {
        let details = this.props.data || this.props.details;
        if (details) {
            if (details.map) {
                this.details = details.map(d => {
                    return {
                        reactKey: d.key,
                        key: this.makeReactElement(d.key),
                        value: this.makeReactElement(d.value),
                    };
                });
            }
        }
    }

    getPrices(showNet) {
        if (!this.props.proposition) {
            return {"basePrice": 0, "bonusPrice": 0};
        }
        let {basePrice, bonusPrice} = this.props;

        let showAs = showNet ? "originalNetPrice" : "originalGrossPrice";
        if (this.props.proposition.payments) {
            // for lp and device list and product details
            basePrice = OnlineUtils.getPaymentsForRole(this.props.proposition.payments, "").basePrice[showAs];
            bonusPrice = OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole).basePrice[showAs];
        } else if (this.props.proposition.monthlyPrices && this.props.proposition.monthlyPrices.length > 0) {// for cart
            showAs = showNet ? "priceNet" : "priceGross";
            bonusPrice = this.props.proposition.monthlyPrices[this.props.proposition.monthlyPrices.length - 1][showAs];
            basePrice = this.props.proposition.monthlyPrices[this.props.proposition.monthlyPrices.length - 1][showAs];
            if (this.props.proposition.monthlyOldPrice && this.props.proposition.monthlyOldPrice[showAs]) {
                basePrice = this.props.proposition.monthlyOldPrice[showAs];
            }
            return {"basePrice": basePrice, "bonusPrice": bonusPrice};
        } else if (this.props.proposition.planTotalMonthlyPrices && this.props.proposition.planTotalMonthlyPrices.length > 0) {
            bonusPrice = this.props.proposition.planTotalMonthlyPrices[this.props.proposition.planTotalMonthlyPrices.length - 1][showAs];
            basePrice = this.props.proposition.planTotalMonthlyPrices[this.props.proposition.planTotalMonthlyPrices.length - 1][showAs];
            if (this.props.proposition.monthlyOldPrice && this.props.proposition.monthlyOldPrice[showAs]) {
                basePrice = this.props.proposition.monthlyOldPrice[showAs];
            }
        }
        if (this.props.proposition.entryType && this.props.proposition.entryType === OfferType.CONVERGENT &&
            this.props.proposition.planTotalMonthlyPrices && this.props.proposition.planTotalMonthlyPrices.length > 0) {
            basePrice = this.props.proposition.planTotalMonthlyPrices[this.props.proposition.planTotalMonthlyPrices.length - 1]["gross"];
            bonusPrice = this.props.proposition.planTotalMonthlyPrices[this.props.proposition.planTotalMonthlyPrices.length - 1]["gross"];
        }
        return {"basePrice": basePrice, "bonusPrice": bonusPrice};
    }

    getModalContent() {
        if (!this.props.proposition) {
            return null;
        }
        const propositionName = this.props.propositionName || (this.props.proposition && this.props.proposition.planName);
        const {basePrice, bonusPrice} = this.getPrices(this.props.showNet);

        return ([
            <div
                key={"large_" + this.props.clientContext + (this.props.proposition.rateplanId || this.props.proposition.productCode)}
                className="u-small-hide">
                <div className="o-modal-content u-padding-all-xl">
                    <table className="u-table-fixed">
                        <caption className="u-text-left u-padding-l">
                            <p className="h3 u-spacing-l">{propositionName}</p>
                            <Price
                                key="large"
                                basePrice={basePrice}
                                bonusPrice={bonusPrice}
                            />
                        </caption>
                        <tfoot>
                            <tr>
                                <td colSpan="2" className="u-small-block u-padding-top-xl">
                                    <p className="u-font-small" dangerouslySetInnerHTML={{__html: this.props.proposition.internetLegal}}/>
                                </td>
                            </tr>
                        </tfoot>
                        {detailsTable(this.details, StylesUtils.large)}
                    </table>
                </div>
            </div>,
            <div
                key={"small" + this.props.clientContext + (this.props.proposition.rateplanId || this.props.proposition.productCode)}
                className="u-hide u-small-block">
                <div className="o-modal-content">
                    <table className="u-table-fixed">
                        <caption className="u-text-left u-padding-xl">
                            <p className="h3 u-spacing-top-m">{propositionName}</p>
                            <Price
                                key="large"
                                basePrice={basePrice}
                                bonusPrice={bonusPrice}
                            />
                        </caption>
                        <tfoot>
                            <tr>
                                <td colSpan="2" className="u-small-block u-padding-top-xl">
                                    <p className="u-font-small" dangerouslySetInnerHTML={{__html: this.props.proposition.internetLegal}}/>
                                </td>
                            </tr>
                        </tfoot>
                        {detailsTable(this.details, StylesUtils.small)}
                    </table>
                </div>
            </div>,
        ]);
    }
}

export class DetailsLarge extends Details {
    constructor(props) {
        super(props);
    }

    render() {
        return (
            <div className="opl-box__details opl-box__details--large">
                <div className="opl-box__details__content">
                    <div className="opl-box__details__content__inner">
                        <div>
                            <p className="h4 u-spacing-l">{this.props.labels.header}</p>
                            {detailsTable(this.details, StylesUtils.large)}
                            <div className="u-spacing-top-l">
                                <a href="#" onClick={this.props.select}>{this.props.labels.select}</a>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

